import { Input, Modal, Icon, Button, Form, Row, Col } from 'antd'
const FormItem = Form.Item;

import { Base64 } from 'js-base64'

////console.log("Base64:",Base64);

class Main extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			visible: false,
			value: Base64.decode(props.value),
			detailHide: props.detailHide ? props.detailHide : false,
		}
	}
	componentWillReceiveProps(nextProps) {
		if (this.props.value !== nextProps.value) {
			this.setState({
				value: Base64.decode(nextProps.value)
			});
		}
	}
	render() {
		let that = this;
		const { visible, value, detailHide } = this.state;
		const { fields } = this.props;
		const specialFields = [{
			id: "currentuser",
			fieldlabelname: ""
		}, {
			id: "currentdept",
			fieldlabelname: ""
		}, {
			id: "wfcreater",
			fieldlabelname: ""
		}, {
			id: "wfcredept",
			fieldlabelname: ""
		}, {
			id: "currentdate",
			fieldlabelname: ""
		}, {
			id: "requestid",
			fieldlabelname: "ID"
		}];
		const mainFields = fields.filter((field) => field.detailtable === "");
		const detailFields = fields.filter((field) => field.detailtable !== "");
		return (
			<div>
				{(value && value != "") && <Button type="primary" onClick={this.showModal.bind(this)} size="small"></Button>}
				{(!value || value == "") && <Button onClick={this.showModal.bind(this)} size="small"></Button>}
				<Modal title={false == detailHide ? "SQL" : ""}
					width={800}
					className="wfdetail-sqlmodal"
					visible={visible}
					onCancel={this.handleOk.bind(this)}
					footer={[
						<Button key="back" type="ghost" size="large" onClick={this.handleClear.bind(this)}> </Button>,
						<Button key="submit" type="primary" size="large" onClick={this.handleOk.bind(this)}> </Button>
					]}>
					<Row>
						<Col>
							{!detailHide && (
								<FormItem
									labelCol={{ span: 4 }}
									wrapperCol={{ span: 20 }}
									label="">
									<div>
										{
											specialFields.map((data) => {
												return (
													<Button type={value.indexOf("$" + data.id + "$") >= 0 ? "primary" : "ghost"} size="small" style={{ marginRight: 10 }} onClick={that.addFieldToSql.bind(this, data)}>[{data.id}] {data.fieldlabelname}</Button>
												)
											})
										}
									</div>
								</FormItem>)}
							{!detailHide && (<FormItem
								labelCol={{ span: 4 }}
								wrapperCol={{ span: 20 }}
								label="">
								<div>
									{
										mainFields.map((data) => {
											return (
												<Button type={value.indexOf("$" + data.id + "$") >= 0 ? "primary" : "ghost"} size="small" style={{ marginRight: 10 }} onClick={that.addFieldToSql.bind(this, data)}>[{data.id}] {data.fieldlabelname}</Button>
											)
										})
									}
								</div>
							</FormItem>)}
							<FormItem
								labelCol={{ span: 4 }}
								wrapperCol={{ span: 20 }}
								label="">
								<div>
									{
										detailFields.map((data) => {
											return (
												<Button type={value.indexOf("$" + data.id + "$") >= 0 ? "primary" : "ghost"} size="small" style={{ marginRight: 10 }} onClick={that.addFieldToSql.bind(this, data)}>[{data.id}] {data.fieldlabelname}</Button>
											)
										})
									}
								</div>
							</FormItem>
						</Col>
					</Row>
					<Input type="textarea" rows={6} value={value} onChange={this.changeSql.bind(this)} />
				</Modal>
			</div>
		)
	}
	changeSql(e) {
		this.setState({
			value: e.target.value
		});
	}
	addFieldToSql(data) {
		////console.log("data:",data);
		let { value } = this.state;
		value += "$" + data.id + "$";
		this.setState({
			value: value
		});
	}
	showModal() {
		this.setState({
			visible: true
		});
	}
	handleOk() {
		const { value } = this.state;
		if (typeof (this.props.onChange) == "function") {
			this.props.onChange(Base64.encode(value));
		}
		this.setState({
			visible: false
		});
	}
	handleClear() {
		if (typeof (this.props.onChange) == "function") {
			this.props.onChange("");
		}
		this.setState({
			visible: false,
			value: ""
		});
	}
}

export default Main


