import { Button, Tabs, Modal } from 'antd'
import { WeaTop } from 'weaCom'
import { routerShape } from 'react-router';

const TabPane = Tabs.TabPane;

import * as DetailActions from '../actions'
import * as API from '../api/detailSet'
import WdBase from './WdBase'
import WdSetSyno from './WdSetSyno'
import Pro from 'promise'

class Main extends React.Component {
    static contextTypes = {
        router: routerShape,
    };
    constructor(props) {
        super(props);
        this.state = {
            datas: [],
            details: [],
            showsyno: false,
        }
        //const {query} = props.location;
    }
    componentDidMount() {
        let that = this;
        const { id } = this.props.params;
        ////console.log("id:",id);
        API.getDetails({ wfId: id }).then((datas) => {
            that.setState({
                details: datas
            });
            return API.getWfNodes({ wfId: id });
        }).then((datas) => {
            ////console.log("datas:",datas);
            that.setState({
                datas: datas
            });
        });
    }
    render() {
        let that = this;
        const { query } = this.props.location;
        const { id } = this.props.params;
        const { datas, details, showsyno } = this.state;
        const { router } = this.context;
        return (
            <div style={{ "height": "100%", "minWidth": "900px" }}>
                <WeaTop title={"[" + id + "]" + query.wfName + "-"} >
                    <Button onClick={
                        () => {

                            Modal.confirm({
                                title: '',
                                content: '',
                                okText: '',
                                cancelText: '',
                                onOk: () => {
                                    this.setState({
                                        showsyno: true,
                                    });
                                },
                                onCancel: () => { }
                            })

                        }
                    }></Button>
                    <Button onClick={
                        () => {
                            // history.pushState(null,"/list");
                            router.push('/list');
                        }
                    }></Button>
                </WeaTop>
                <WdSetSyno showsyno={showsyno} wfId={id} onOk={() => {
                    this.setState({
                        showsyno: false,
                    });
                }}> </WdSetSyno>
                <div className="code-box-demo" style={{ "marginTop": "5pwx" }}>

                    <div style={{ "padding": "10px" }} className="card-container">
                        <Tabs type="card" defaultActiveKey={datas[0] ? datas[0].id : "" || ""}
                            onChange={(nodeId) => {

                            }}
                        >
                            {
                                datas.map((data, i) => {
                                    return (
                                        <TabPane tab={data.name + "[" + data.id + "]"} key={data.id}>
                                            <WdBase wfId={id} nodeId={data.id} details={details} datasMap={datas} />
                                        </TabPane>
                                    )
                                })
                            }
                        </Tabs>
                    </div>
                </div>
            </div>

        )
    }
}

export default Main


