import {Input,Modal,Icon,Button,Table,Select} from 'antd'
const Option = Select.Option;

const columns = [{
  title: '',
  dataIndex: 'value',
  key: 'value',
}, {
  title: '',
  dataIndex: 'type',
  key: 'type',
  render(text,record,index) {
  	return (
  		<Select value={text} style={{width:"100%"}}>
  			<Option value="3_1"></Option>
  			<Option value="3_4"></Option>
  			<Option value="3_164"></Option>
  			<Option value="3_251"></Option>
  		</Select>
  	)
  }
}];

class Main extends React.Component {
	constructor(props) { 
		super(props);
		this.state = {
			visible:false,
			datas:[{
			  key: '1',
			  value: '0',
			  type: '3_1'
			}, {
			  key: '2',
			  value: '1',
			  type: '3_4'
			}, {
			  key: '3',
			  value: '2',
			  type: '3_164'
			}, {
			  key: '4',
			  value: '3',
			  type: '3_251'
			}]
		}
	}
	render() {
		let {datas} = this.state;
		const {visible} = this.state;
		const {value} = this.props;
//		//console.log('-------------value in props',value)
//		//console.log('-------------datas in state',datas)
		if(value && value!="") datas = JSON.parse(value);
		////console.log("fields:",fields);
		return (
			<div>
			{(value && value!="") && <Button type="primary" onClick={this.showModal.bind(this)} size="small"></Button>}
			{(!value || value=="") && <Button onClick={this.showModal.bind(this)} size="small"></Button>}
			<Modal title="" 
		   		  visible={visible}
		   		  onCancel={this.handleOk.bind(this)}
			   	  footer={[
			          <Button key="back" type="ghost" size="large" onClick={this.handleClear.bind(this)}> </Button>,
			          <Button key="submit" type="primary" size="large" onClick={this.handleOk.bind(this)}> </Button>
			      ]}>
				<Table dataSource={datas} columns={columns} size="middle" />
			</Modal>
			</div>
		)
	}
	showModal() {
		this.setState({
			visible:true
		});
	}
	handleOk() {
		const {datas} = this.state;
		////console.log("save datas:",datas);
		if(typeof(this.props.onChange)=="function") {
			this.props.onChange(JSON.stringify(datas));
		}
		this.setState({
			visible:false
		});
	}
	handleClear() {
		if(typeof(this.props.onChange)=="function") {
			this.props.onChange("");
		}
		this.setState({
			visible:false
		});
	}
}

export default Main


