import React, { Component, PropTypes } from 'react'
import { render } from 'react-dom'
//const ReactRouter = require('react-router');
//let { Router, Route, Link ,browserHistory, useRouterHistory } from 'react-router';
import Router from 'react-router/lib/Router'
import Route from 'react-router/lib/Route'
import browserHistory from 'react-router/lib/browserHistory'
import useRouterHistory from 'react-router/lib/useRouterHistory'
import IndexRoute from 'react-router/lib/IndexRoute'

import { createHistory, useBasename, createHashHistory } from 'history'
import { Breadcrumb } from 'antd'

import WdSets from './WdSets'
import WdSet from './WdSet'
import './index.css'

const history = useRouterHistory(createHashHistory)();

class Root extends React.Component {
    render() {
        return (
           <Router history={history}>
                <Route name="/" breadcrumbName="" path="/">
                	<IndexRoute component={WdSets} />
                	<Route name="list" path="list" breadcrumbName="" component={WdSets} >
                    	<Route name="card" path=":id/show" breadcrumbName="" component={WdSet} />
                    </Route>
                </Route>
           </Router>
        )
    }
}

render(<Root />,document.getElementById("container"))


