		const datasMapV2 = [];
		for (let i = 0; i < datasMap.length; i++) {
			if (nodeId != datasMap[i].id) {
				datasMapV2.push(datasMap[i]);
			}
		}
		this.setState({
			details: details,
			detailtable: detailtable,
			datasMap: datasMapV2
		});
		// let that = this;
		// const {wfId} = this.props;
