	setDatas(detailtable) {
		let that = this;
		const { setFieldsValue } = this.props.form;
		const { wfId, nodeId } = this.props;
		//const {nownodeid} = this.state;
		//console.log("oldnodeid:",nodeId," newnodeId:",nownodeid);
		API.getDetailBase({ wfId: wfId, nodeId: nodeId, detailtable: detailtable }).then((data) => {
			////console.log("data:",data);
			let opType = "add";
			let id = "";
			if (data.id) {
				opType = "edit";
				id = data.id;
			}
			////console.log("data.impTypeAuth:",data.impTypeAuth);
			setFieldsValue({
				detailtable: data.detailtable,
				paging: data ? data.paging : false,
				searching: data ? data.searching : false,
				searchingforcol: data ? data.searchingforcol : false,
				excelImport: data ? data.excelImport : false,
				excelOutput: data ? data.excelOutput : false,
				canAdd: data ? data.canAdd : true,
				canDel: data ? data.canDel : true,
				canEditOld: data ? data.canEditOld : true,
				canDelOld: data ? data.canDelOld : true,
				canRemark: data ? data.canRemark : true,
				showSN: data ? data.showSN : false,
				treetable: data ? data.treetable : false,
				modal: data ? data.modal : false,
				mustAdd: data ? data.mustAdd : false,
				defaultRow: (data && data.defaultRow) ? parseInt(data.defaultRow) : 0,
				treenode: data ? data.treenode : "",
				excelOutputAll: data ? data.excelOutputAll : false,
				width: data ? data.width || "100%" : "100%",
				impTypeAuth: data && data.impTypeAuth && data.impTypeAuth != "" ? JSON.parse(data.impTypeAuth) || [0] : [0],
				mustHide : data && data.mustHide && data.mustHide != ""&& data.mustHide != "null" ?data.mustHide:"",
				height: data.height || "0",
				pageSize :data.pageSize || "10",
				tdatashowonly: data.tdatashowonly || false,
			});
			const detailFields = data.wdfl;
			that.setState({
				id: id,
				opType: opType,
				detailFields: detailFields,
				mustHide : data  && data.mustHide && data.mustHide != ""&& data.mustHide != "null"?data.mustHide:"",
			});
			that.setChDetail(wfId, nodeId, detailFields);
			////console.log("chDetailArr:",chDetailArr);
		});

	}
