import React, { Component, PropTypes } from 'react'
import { Button, Tabs, Modal, Table ,message} from 'antd'
import * as API from '../api/detailSet'


class WdSetSyno extends Component {
  static defaultProps = {
  }
  constructor(props) {
    super(props);
    this.state = {
      datasMap: [],
      selectedRowKeys: [],  // 
      loading: false,
    }
  }
  componentDidMount() {
    let that = this;
    const { wfId } = that.props;
    API.getWfVersion({ wfId }).then((datas) => {
      that.setState({
        datasMap: datas
      });
    });
  }
  // onSelectChange(selectedRowKeys) {
  //   console.log('selectedRowKeys changed: ', selectedRowKeys);

  //   let that = this;
  //   console.log("onSelectChange that", that);
  //   that.setState({
  //     selectedRowKeys: selectedRowKeys
  //   });
  // }
  saveWfid(wfId, selectedRowKeys) {
    if(selectedRowKeys.length == 0){
      message.error("ID", 10);
    }else if (selectedRowKeys.length > 0 && (wfId === selectedRowKeys[0])){
      message.error("ID", 10);
    }
    else if (selectedRowKeys.length > 0 && (wfId !== selectedRowKeys[0])) {
      API.saveWfVersion({ wfId, selectedRowKeys }).then((msg) => {
        if (msg.states == true) {
          message.success("", 10);
          setTimeout(function () {
            window.location.reload(true);
          }, 3000);
        } else {
          message.error("", 10);
        }
      });
    }
  }
  render() {
    let that = this;
    const { showsyno, wfId } = that.props;
    const columns = [{
      title: 'id',
      dataIndex: 'id',
    }, {
      title: '',
      dataIndex: 'workflowname',
    }, {
      title: '',
      dataIndex: 'version',
    }, {
      title: '',
      dataIndex: 'versionDescription',
    }];

    const { loading, selectedRowKeys, datasMap } = that.state;

    const rowSelection = {
      selectedRowKeys,
      type: "radio",
      onChange: (selectedRowKeys) => that.setState({
        selectedRowKeys: selectedRowKeys,
      })
    };
    return (
      <Modal title={""}
        visible={showsyno}
        footer={[
          (<Button key="back" type="ghost" size="large" onClick={() => { that.props.onOk() }}>
            {''}
          </Button>),
          (<Button key="submit" loading={loading} type="primary" size="large" onClick={() => {
            that.saveWfid(wfId, selectedRowKeys);
            that.props.onOk();
          }} >
            {''}
          </Button>)
        ]}><div>
          <Table rowSelection={rowSelection} columns={columns} dataSource={datasMap} /></div>
      </Modal>
    )
  }
}

export default WdSetSyno;



