import Pro from 'promise';

import {Table,Button,Input} from 'antd'
import {WeaTop} from 'weaCom'
import { routerShape } from 'react-router';

import * as API from '../api/detailSet'

class MainList extends React.Component {
	static contextTypes = {
    router: routerShape,
  };
	constructor(props) { 
		super(props);
		this.state = {
			searchVal:"",
			datas:[],
			dataSource:[],
			current:1
		}
	}
	componentDidMount() {
		let that = this;
		API.getWfs().then(function(datas) {
			that.setState({
				datas:datas,
				dataSource:datas
			});
		});
	}
	render() {
        return this.props.children || this.renderList();
    }
	renderList() {
		const {history} = this.props;
    const {dataSource,datas,current,searchVal} = this.state;
		const { router } = this.context;
		let that = this;
 		const columns = [{
			title: 'ID',
			dataIndex: 'wfId',
			key: 'wfId'
		},{
			title: '',
			dataIndex: 'wfName',
			key: 'wfName'
		},{
			title: '',
			dataIndex: 'typeName',
			key: 'typeName'
		},{
			title: '',
			dataIndex: '',
			key: 'op',
			render(text,record,index) {
				return (
					<span>
		  			<a href="javascript:void(0)" onClick={
		  				()=>{
                router.push({
                  pathname: `/list/${record.wfId}/show`,
                  query: { wfName: record.wfName },
                })
		  					// history.pushState(null,"/list/"+record.wfId+"/show",{wfName:record.wfName});
		  				}
		  			}></a>
		  			</span>
				)
			}
		}];
		const pagination = {
			current:current,
			pageSize:10,
			total:dataSource.length,
			onChange(current) {
				that.setState({
					current:current
				});
			}
		};
		return (
			<div style={{"height":"100%"}}>
				<WeaTop title=""></WeaTop>
				<div style={{"paddingLeft":"20px","paddingRight":"20px","margin":"0 auto","marginTop":"20px"}}>
					<Input placeholder="" 
						onChange={(e)=>{
							let dataSource = new Array();
							const value = e.target.value;
							////console.log("datas:",datas);
							////console.log("val:",value);
							if(!value || value=="") {
								that.setState({
									searchVal:value,
									dataSource:datas
								});
							}
							else {
								datas.map((data,index)=>{
									let find = false;
									for(let p in data) {
										if(!find && data[p].indexOf(value)>=0) {
											dataSource.push(data);
											find = true;
										}
									}
								});
								that.setState({
									searchVal:value,
									dataSource:dataSource,
									current:1
								});
							}
						}} value={searchVal} style={{"width":"250px","marginBottom":"10px"}} />
					<Table pagination={pagination} dataSource={dataSource} columns={columns} />
				</div>
			</div>
		)
	}
}

export default MainList


