//import Pro from 'promise'

const getFd = (values) => {
	let fd = "";
	//var data = new FormData();
	for(let p in values) {
		const value = values[p];
		fd += p+"="+encodeURIComponent(value)+"&";
		//data.append(p,values[p]);
	}
	if(fd!="") {
		fd = fd.substring(0,fd.length-1);
	}
	////console.log("fd:",fd);
	return fd
}

export const getWfs = (params) => {
	return new Promise((resolve,reject) => {
		//"/cloudstore/app/no0000012/ControlServlet.jsp?action=Action_DoWfDetailGet"
		fetch("/api/app/no0000012/set/wfs",{
            method: 'post',
            headers: {
            	'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
            	'X-Requested-With':'XMLHttpRequest'
            },
            credentials: 'include'
            //body: JSON.stringify(data)
        }).then(function(response) {
            ////console.log("data:",data);
            ////console.log("response.json():",response);
            const datas = response.json();
            if(datas && datas.status && datas.status==="false") {
				reject(datas.error?datas.error:"");
			}
			else {
	        	resolve(datas);
	        }
        }).catch(function(ex) {
           	reject("AJAX:",ex);
            //console.log('parsing failed', ex)
        });
	});
}

